import { Component, OnInit, Input, HostBinding, ViewChild, TemplateRef } from '@angular/core';

declare var System: any;

@Component({
  selector: 'wf-approval-logs',
  templateUrl: './wf-approval-logs.component.html',
  styleUrls: ['./wf-approval-logs.component.css'],
})
export class WfApprovalLogsComponent implements OnInit {

  dataId: string; // 单据id
  dataIdSubStr: string;//单据id 的 subStr，解决子表dataId还要逗号，querySelect报错问题
  procInstId: string; // 流程实例id
  ifForecast: boolean = false; // 是否预测日志
  type: string;//查看日志类型
  @HostBinding('class') cls = ''; // 弹框显示时，增加样式

  @Input() ShowHeader: boolean = false;//是否显示日志头部
  @Input() ShowViewProcess: boolean = false;//是否显示查看流程按钮
  @Input() OnlyIncludeCurrentProcInst: boolean = false;//是否只包含当前流程的日志
  @Input() IncludeAdminOperate: boolean = false;//是否显示操作日志
  @Input() IncludeHistory: boolean = false;//是否包含历史日志
  @ViewChild('taskDialogFooter') taskDialogFooter: TemplateRef<any>;//底部区域

  logContainer: any;//日志容器

  @Input()
  set DataId(dataId: string) {
    if (dataId) {
      this.dataId = dataId;
      this.dataIdSubStr = dataId.substring(0, 5);
      this.getTaskLogs();
    }
  }

  @Input()
  set ProcInstId(procInstId: string) {
    if (procInstId) {
      this.procInstId = procInstId;
      this.getTaskLogs();
    }
  }

  @Input()
  set ActivityDefinitionId(actDefId: string) {
    if (actDefId) {
      this.getTaskLogs(actDefId);
    }
  }

  @Input()
  set IfForecast(ifForecast: string) {
    if (ifForecast) {
      this.ifForecast = ifForecast == 'true' ? true : false;
      this.getTaskLogs();
    }
  }

  @Input()
  set Type(type: string) {
    if ((type == 'process' || type == 'activity') && this.type != type) {
      this.type = type;
      if (type === 'process') {// type = 'process'查看流程日志；type = 'activity'，查看节点日志 sequence，转移线
        this.getTaskLogs();
      }
    }
  }

  @Input()
  set processForecastPayload(payload: any) {
    if (payload) {
      this.ifForecast = true;
      this.getTaskLogs('', payload);
    }
  }

  @Input() Style: string;//样式，card 卡片版；datagrid 表格版

  constructor() { }

  ngOnInit() {
    // this.getTaskLogs();
  }

  fill() {
    this.cls = 'd-flex flex-fill h-100 f-utils-absolute-all px-4 py-3';
  }

  private getTaskLogs(actDefId?: string, wfForecastPayload?: any) {
    if (this.dataId || this.procInstId || wfForecastPayload) {
      System.import('/platform/runtime/wf/webapp/mobiletaskcenter/taskComment.js' + '?v=20230602').then(() => {
        if (this.dataId) {
          this.logContainer = window.document.querySelector('.dataId' + this.dataIdSubStr);
        } else {
          this.logContainer = window.document.querySelector('.procInstId' + this.procInstId);
        }
        let payload: any = {
          bizInstId: this.dataId,
          processInstanceId: this.procInstId,
          activityDefinitionId: actDefId,
          container: this.logContainer,
          terminal: 'web',
          typeId: 'wf',
          ifForecast: this.ifForecast,
          showHeader: this.ShowHeader,
          showViewProcess: this.ShowViewProcess,
          wfForecastPayload: wfForecastPayload,
          onlyIncludeCurrentProcInst: this.OnlyIncludeCurrentProcInst,
          isIncludeHistory: this.IncludeHistory,
          includeRetrieve: this.IncludeAdminOperate,
          includeAdminOperate: this.IncludeAdminOperate,
        };
        if (this.Style && this.Style == 'datagrid') {//表格版
          (window as any).gspTaskCenterBizLog.ApprovalLogUtil.getWebHtml(payload).then((result) => { });
        } else {//卡片版
          (window as any).gspTaskCenterBizLog.ApprovalLogUtil.getWebLogsHtml(payload).then((result) => { });
        }
      });
    }
  }

  /**
   * 是否显示操作日志
   */
  ifShowOperateLogChange() {
    this.getTaskLogs();
  }

  /**
  * 国际化
  */
  i18nTranslate(code: string) {
    var result: string;
    const supportLanguage = ["zh-CHS", "zh-CHT", "en"];
    const curLanguage = localStorage.getItem("languageCode");
    const translate = {
      "zh-CHS": {
        showOperateLog: "显示操作日志"
      },
      "zh-CHT": {
        showOperateLog: "顯示操作日誌"
      },
      en: {
        showOperateLog: "Show operate log"
      },
    };
    if (curLanguage && supportLanguage.indexOf(curLanguage) > -1) {
      result = translate[curLanguage][code];
    } else {
      result = translate["zh-CHS"][code];
    }
    return result;
  }
}
